td, th {
position: relative;
width:5vw;
border-color: transparent;
border-width: 1px;
border-style: solid;
align-items: center;
justify-items: center;
}

td:nth-child(3n) {
border-right-width:5px ;
}

tr:nth-child(3n) td {
border-bottom-width: 5px ;
}

/* tr:nth-child(3n) > td{
border-bottom: transparent;
} */

tr:last-child td{
  border-bottom: transparent;
}

td:last-child{
  border-right: transparent;
}

tr:first-child td{
border-top: transparent;
}

tr:last-child{
border-bottom: transparent;
}

td:first-child{
border-left: transparent;
}

td:hover{
z-index: 1;
-webkit-transform: scale(1.2);
        transform: scale(1.2);
}

@media screen and (max-width: 500px){
table{
    width: 100vmin;
    height: 100vmin;  
}
}
